<template>
    <div class="profile">
        <h1>1</h1>
        <button @click="choose">按钮</button>
        <h1>{{this.selectMarketer}}</h1>
    </div>
</template>
<script>
export default {
    name:'Profile',
    data() {
        return {
            marketerList:[{id:1,name:'a'},{id:2,name:'b'}],
            selectMarketer:{}
        }
    },
    methods: {
        choose() {
            this.selectMarketer = this.marketerList.find(item => item.id == 2)
        }
    }
}
</script>
